.collapse {
  background-color: rgb(255, 96, 96);
  border-radius: 0.625rem;
  overflow: hidden;
  margin-bottom: 1.5rem;
  width: 36.375rem;
}
.collapse-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 3.25rem;
  padding: 0 1rem;
  font-size: 1.125rem;
  font-weight: 500;
  color: white;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
}
.collapse-header h2 {
  margin: 0;
  font-size: 1.125rem;
}
.collapse-header .chevron {
  width: 1.5rem;
  transition: transform 0.3s ease;
}
.collapse-header .chevron.open {
  transform: rotate(180deg);
}
.collapse-header .chevron img {
  width: 100%;
  height: auto;
  display: block;
}
.collapse-content {
  background-color: #f6f6f6;
  color: #000;
  font-size: 1.125rem;
  line-height: 1.5;
  padding: 1rem;
}
.collapse-content ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.collapse-content ul li {
  margin-bottom: 0.5rem;
}