.collapse {
    background-color: rgba(255, 96, 96, 1);
    border-radius: 0.625rem; // 10px
    overflow: hidden;
    margin-bottom: 1.5rem;
    width: 36.375rem; // âœ… 582px fixe

    &-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 3.25rem; // 52px
        padding: 0 1rem;
        font-size: 1.125rem; // 18px
        font-weight: 500;
        color: white;
        cursor: pointer;
        -webkit-user-select: none;
        user-select: none;

        h2 {
            margin: 0;
            font-size: 1.125rem;
        }

        .chevron {
            width: 1.5rem; // 24px
            transition: transform 0.3s ease;

            &.open {
                transform: rotate(180deg);
            }

            img {
                width: 100%;
                height: auto;
                display: block;
            }
        }
    }

    &-content {
        background-color: #f6f6f6;
        color: #000;
        font-size: 1.125rem;
        line-height: 1.5;
        padding: 1rem;

        ul {
            list-style-type: none;
            padding: 0;
            margin: 0;

            li {
                margin-bottom: 0.5rem;
            }
        }
    }
}